@use 'base/mixins' as m;
@use 'base/variables' as v;


.formulario{
    width: 100%;
}
.campo{
    display: flex;
    gap: 1rem;
    margin-bottom: 2rem;
    align-items: center;
    label{
        flex: 0 0 10rem
    }
    input{
        flex: 1;
        border: none;
        padding: calc(v.$separacion / 4);
        border-radius: 1rem;
    }
    select{
        flex: 1;
        border: none;
        padding: calc(v.$separacion / 4);
        border-radius: 1rem;
    }
}