@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-caja{
    width: 95%;

    margin: 0 auto;
    display: block;
    @include m.tablet { 
        margin-top: 4rem;
        display: flex;
        max-width: 1200px;
    }
}
.caja{
    @include m.tablet { 
        width: 20rem;
        height: 20rem;
    }
    width: 95%;
    height: 95%;
    background: v.$blanco;
    margin: 1rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 1rem;
    box-shadow: v.$box-shadow;
    padding: 1rem;
    cursor: pointer;
    border: v.$border;
    color: v.$negro;
    i{
        font-size: 12rem;
        color: darken(v.$verde,5%);
    }
    h5{
        font-size: 2.5rem;
        margin-top: 2rem;
    }
    &:hover{
        color: v.$blanco;
        background-color: v.$verde;
        i{
            color: v.$blanco;
        }
    }

}
.total{
    padding: 1rem;
    background-color: v.$rojo;
    border: v.$border;
    box-shadow: v.$box-shadow;
}