/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

::selection {
  background: #f86011; /*Color naranja para el fondo*/
  color: #fff; /*Color blanco para el texto seleccionado*/
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.header {
  background-color: #e1e1e1;
  padding: 1rem 0 3rem 0;
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header .logo {
  font-size: 3rem;
  display: flex;
  justify-items: center;
}
.contenido-header .logo i {
  color: rgb(0, 110, 46);
  margin-right: 0.5rem;
}
.contenido-header .logo span {
  font-size: 3rem;
  color: #1a1b15;
  font-weight: 700;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s ease-in-out, opacity 0.3s linear, height 0.5s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #1a1b15;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: rgb(0, 110, 46);
}

.mobile-menu {
  margin-top: 2rem;
  margin-bottom: 2rem;
  width: 5rem;
  height: 5rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu i {
  width: 100%;
  color: #1a1b15;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.navegacion-nuevo {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-nuevo {
    display: flex;
    align-items: center;
    opacity: 1;
  }
}
.navegacion-nuevo a {
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-nuevo a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion-nuevo a:last-of-type {
    margin-right: 0;
  }
}

.home {
  display: block;
  align-items: center;
  padding: 2rem;
}
@media (min-width: 768px) {
  .home {
    display: grid;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.btn {
  margin-top: 1rem;
  padding: 0.5rem;
  padding-left: 1rem;
  border-radius: 0.5rem;
  border: 0.2rem solid rgb(0, 110, 46);
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
  color: rgb(0, 110, 46);
  cursor: pointer;
  font-size: 1.7rem;
}
.btn span {
  padding: 0.7rem 1rem;
  border-radius: 0.5rem;
  background: rgb(0, 110, 46);
  color: #FFFFFF;
  margin-left: 0.5rem;
}
.btn:hover {
  background: rgb(0, 110, 46);
  color: #FFFFFF;
}
.btn:hover span {
  margin-left: 1rem;
}

.contenedor-caja {
  width: 95%;
  margin: 0 auto;
  display: block;
}
@media (min-width: 768px) {
  .contenedor-caja {
    margin-top: 4rem;
    display: flex;
    max-width: 1200px;
  }
}

.caja {
  width: 95%;
  height: 95%;
  background: #FFFFFF;
  margin: 1rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 1rem;
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
  padding: 1rem;
  cursor: pointer;
  border: 0.2rem solid rgb(0, 110, 46);
  color: #1a1b15;
}
@media (min-width: 768px) {
  .caja {
    width: 20rem;
    height: 20rem;
  }
}
.caja i {
  font-size: 12rem;
  color: #005523;
}
.caja h5 {
  font-size: 2.5rem;
  margin-top: 2rem;
}
.caja:hover {
  color: #FFFFFF;
  background-color: rgb(0, 110, 46);
}
.caja:hover i {
  color: #FFFFFF;
}

.total {
  padding: 1rem;
  background-color: rgb(169, 3, 41);
  border: 0.2rem solid rgb(0, 110, 46);
  box-shadow: 0.5rem 0.5rem 0 rgba(22, 160, 133, 0.2);
}

.formulario {
  width: 100%;
}

.campo {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo select {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}

.boton {
  background-color: rgb(0, 110, 46);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #003b19;
  cursor: pointer;
}

.boton-editar {
  background-color: #FFD700;
  padding: 1.5rem 4rem;
  color: #1a1b15;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  margin: 1rem 0;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-editar {
    width: auto;
    width: 100%;
  }
}
.boton-editar:hover {
  background-color: #ccac00;
  cursor: pointer;
}

.boton-eliminar {
  background-color: rgb(169, 3, 41);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  margin: 1rem 0;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
    width: 100%;
  }
}
.boton-eliminar:hover {
  background-color: #77021d;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  border-radius: 0.5rem;
  background-color: rgb(169, 3, 41);
}
.alerta.exito {
  border-radius: 0.5rem;
  background-color: rgb(0, 110, 46);
}

table {
  background-color: #1a1b15;
  text-align: left;
}
@media (min-width: 768px) {
  table {
    width: 100%;
  }
}

th, td {
  border: 0.2rem solid rgb(0, 110, 46);
  padding: 2rem;
}

thead {
  background-color: #005523;
  border-bottom: solid 2rem #e1e1e1;
  color: #FFFFFF;
}

tr:nth-child(even) {
  background-color: #1a1b15;
}

tr:hover {
  background-color: rgb(0, 110, 46);
  color: #FFFFFF;
}