@use 'base/mixins' as m;
@use 'base/variables' as v;

table{
    background-color: v.$negro;
    text-align: left;
    @include m.tablet { 
        width: 100%;
    }
    
}

th, td{
    border: v.$border;
    padding: 2rem;
}

thead{
    background-color:darken(v.$verde,5%);
    border-bottom: solid 2rem v.$gris;
    color: v.$blanco;

}
tr:nth-child(even){
    background-color: v.$negro;
}
tr:hover{
    background-color: v.$verde;
    color: v.$blanco;
}