@use 'base/mixins' as m;
@use 'base/variables' as v;


.home{
    display: block;
    align-items: center;
    padding: 2rem;
    @include m.tablet { 
        display:grid;
        @include m.grid(2, v.$separacion);
    }
}
.app{
    width: 95%;
    margin: 0 auto;
    padding: 3rem 0;
}
.nombre-pagina{
    text-align: center;
    margin: 5rem 0;
    font-weight: v.$bold;
}
.descripcion-pagina{
    text-align: center;
}

.btn{
    margin-top: 1rem;
    padding: .5rem;
    padding-left: 1rem;
    border-radius: .5rem;
    border: v.$border;
    box-shadow: v.$box-shadow;
    color: v.$verde;
    cursor: pointer;
    font-size: 1.7rem;
    span{
        padding: .7rem 1rem;
        border-radius: .5rem;
        background: v.$verde;
        color: v.$blanco;
        margin-left: .5rem;

    }
    &:hover{
        background: v.$verde;
        color: v.$blanco;
        span{
            margin-left: 1rem;
        }
    }
}