@use 'base/mixins' as m;
@use 'base/variables' as v;
.header{
    background-color: v.$gris;
    padding: 1rem 0 3rem 0;
    box-shadow: v.$box-shadow;
        &.inicio{
            background-position: center center;
            background-size: cover;
            @include m.tablet{
                min-height: 70rem;
            }
    
            .contenido-header{
                @include m.tablet{
                    min-height: 70rem;
                }
            }
        }
    }
    .contenido-header{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        .logo{
            font-size: 3rem;
            display: flex;
            justify-items: center;
            i{
                color: v.$verde;
                margin-right: .5rem;
            }
            span{
                font-size: 3rem;
                color: v.$negro;
                font-weight: v.$bold;
            }
        }
    }
    .derecha{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet{
            align-items: flex-end;
        }
    }
    .barra{

        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 3rem;
        @include m.tablet{
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
        
    }
    .navegacion{
    
        visibility: hidden;
        opacity: 0;
        transition: visibility 0.2s ease-in-out, opacity .3s linear, height .5s linear;
        height: 0;
        @include m.tablet { 
            display: flex;
            align-items: center;
            opacity: 1;
            visibility: visible;
        }
        a{
            color: v.$negro;
            text-decoration: none;
            font-size: 2rem;
            display: block;
            text-align: center;
            @include m.tablet{
                margin-right: 2rem;
                font-size: 1.8rem;
                &:last-of-type{
                    margin-right: 0;
                }
            }

            &:hover{
                color: v.$verde;
            }
        }
    }
.mobile-menu{

        margin-top: 2rem;
        margin-bottom: 2rem;
        width: 5rem;
        height: 5rem;
        @include m.tablet{
            display: none;
        }
        i{
            width: 100%;
            color: v.$negro;
        }
    }
    
.mostrar{
        visibility: visible;
        opacity: 1;
        height: auto;
}

.navegacion-nuevo{
    margin-bottom: 1rem;
    @include m.tablet { 
        display: flex;
        align-items: center;
        opacity: 1;
    }
    a{
        text-decoration: none;
        font-size: 2rem;
        display: block;
        text-align: center;
        @include m.tablet{
            margin-right: 2rem;
            font-size: 1.8rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
    }
}